import Vue from 'vue'
import Vuex from 'vuex'

import Config from '../service/config/config.js';
import Common from '../service/config/common.js';

Vue.use(Vuex)

const store = new Vuex.Store({
	state: {
		baseUrl: Config.SERVER_IP + ':' + Config.SERVER_PORT + Config.TRADE_ACTION,
		WSUrl: Config.WSSERVER_IP + ':' + Config.SERVER_PORT + Config.WEBSOCKE_ACTION,
		//
		forcedLogin: false,
		hasLogin: false,
		userName: "",
		count:1,
		//websocket
		connected: false,
		connecting: false,
		msg: false,
		// push 
		contrary:false,
		numders:0,
		accountType:'',
		phoneNo:'',
		score:'',
		token:''
	},
	//  vue  computed ()
	//getters vuexGettersGetters state
	getters:{
		getStateCount(state){ return state.count + 1 }
	},
	mutations: {
		//
		add(state){ state.count = state.count + 1 },
		reduction(state){ state.count = state.count - 1 },
		// push 
		push(state){ state.contrary = !state.contrary },
		//  
		addnum(state){ state.numders = state.numders + 1 },
		
		login(state, userName) {
			state.userName = userName || '';
			state.hasLogin = true;
		},
		logout(state) {
			state.userName = "";
			state.hasLogin = false;
		},
		//
		authorizeLogin(state, params){
			var resultObj = Common.tradeRequest(params.data.jydm,params.data.body);
			console.log('publicSave==JSON.stringify(resultObj):'+ JSON.stringify(resultObj))
			uni.showLoading({
				title: '',
				mask:true
			});
			uni.request({
				method:'POST',
				url: state.baseUrl,
				async : false,
				cache : false,
				data: "params={imp:" + JSON.stringify(resultObj) + "}",
				header: {
					'content-type':'application/x-www-form-urlencoded'
				},
				success: (res) => {
					console.log(res)
					uni.hideLoading()
					if(res.data.imp.head[0].respcode =='00000'){
						uni.showToast({
							title: res.data.head.msgMsg,
							duration: 3000,
							icon:'none'
						});
						uni.setStorage({
						key:'shtorgObj',
						data:JSON.stringify( res.data.body)
						})
						
						params.success(res.data)
						state.token = res.data.body.token
						
						uni.switchTab({
							url: '../../pages/main/main'
						});
					}if(res.data.imp.head[0].respcode ==''){
						
						uni.showToast({
							//title: '',
							title: res.data.imp.head[0].respmsg,
							duration: 3000,
							icon:'none'
						});
					}else if(res.data.imp.head[0].respcode =='44444'){
						uni.showToast({
							title: res.data.imp.head[0].respmsg,
							duration: 3000,
							icon:'none'
						});
					}else if(res.data.imp.head[0].respcode =='ERROR'){
						uni.showToast({
							title: res.data.imp.head[0].respmsg,
							duration: 3000,
							icon:'none'
						});
					}else {
						uni.showToast({
							title: '',
							duration: 3000,
							icon:'none'
						});
					}
				},
				fail:(res) => {
					uni.hideLoading();
					uni.showToast({
						title: '',
						duration: 3000,
						icon:'none'
					});
				}
			});
		},
		publicRequest(state, params){
			var resultObj = Common.tradeRequest(params.data.jydm,params.data.body);
			console.log('publicSave==JSON.stringify(resultObj):'+ JSON.stringify(resultObj))
			uni.showLoading({
				title: '',
				mask:true
			}); 
			uni.request({
				method:'POST',
				url: state.baseUrl,
				async : false,
				cache : false,
				data: "params={imp:" + JSON.stringify(resultObj) + "}",
				header: {
					'content-type':'application/x-www-form-urlencoded'
				},
				success: (res) => {
					console.log(res)
					uni.hideLoading()
					if(res.data.imp.head[0].respcode =='00000'){
						params.success(res.data)
					}else if(res.data.imp.head[0].respcode =='44444'){
						uni.showToast({
							title: res.data.imp.head[0].respmsg,
							duration: 3000,
							icon:'none'
						});
					}else if(res.data.imp.head[0].respcode =='ERROR'){
						uni.showToast({
							title: res.data.imp.head[0].respmsg,
							duration: 3000,
							icon:'none'
						}); 
					}else {
						uni.showToast({
							title: '',
							duration: 3000,
							icon:'none'
						});
					}	
				},
				fail:(res) => {
					uni.hideLoading();
					uni.showToast({
						title: '',
						duration: 3000,
						icon:'none'
					});
				}
			});
		},
		WSConnect(state,params) {
			if (state.connected || state.connecting) {
				uni.showModal({
					content: ',!',
					showCancel: false
				})
				return false
			}
			state.connecting = true 
			uni.showLoading({
				title: '...'
			})
			uni.connectSocket({ 
				url: state.WSUrl + params.data.userid +'?'+ params.data.username, 
				async : false,
				cache : false, 
				method:'POST',
				header: {
					'content-type':'application/x-www-form-urlencoded'
				},
				success(res) {
					// 
				},
				fail(err) {
					// 
				}
			})
			uni.onSocketOpen((res) => {
				state.connecting = false
				state.connected = true 
				uni.hideLoading()
				uni.showToast({
					icon: 'none',
					title: ''
				})
				console.log('onOpen', res);
			})
			uni.onSocketError((err) => {
				state.connecting = false
				state.connected = false
				uni.hideLoading()
				uni.showModal({
					content: 'websocket',
					showCancel: false
				})
				console.log('onError', err);
			})
			uni.onSocketMessage((res) => { 
				console.log('onMessage', res)
				params.success(res,state.connected)
			})
			uni.onSocketClose((res) => {
				state.connected = false 
				console.log('onClose', res)
			})
		},
		WSSend(state, params){
			var resultObj = Common.tradeRequest(params.data.jydm,params.data.body); 
			uni.sendSocketMessage({ 
				data: "{imp:" + JSON.stringify(resultObj) + "}",
				success(res) { 
					console.log(res); 
				},
				fail(err) {
					console.log(err);
				}
			})
		},
		WSClose() {  
			uni.closeSocket({ 
				
				success(res) {
					console.log(res);
				},
				fail(err) {
					console.log(err);
				}
			})
		}, 
	},
	//  mutations  state ,
	// mutations  state ,
	// actions  mutations 
	actions:{
		// 
		addfun(ele){ ele.commit("add") },
		reductionfun(ele){ ele.commit("reduction") },
		// push 
		pushfun(ele){ ele.commit("push") },
		//  
		addnumfun(ele){ ele.commit("addnum") },
	}
})

export default store
